<template>
  <div>
    <!--<v-ons-card class="menu-icon">
        <v-ons-icon icon="fa-bars"></v-ons-icon>
    </v-ons-card>-->



    <!--<v-ons-fab class="menu-icon" position='bottom center' ripple="true" modifier="mini" :visible="fabVisible">
      <v-ons-icon icon="fa-bars"></v-ons-icon>
    </v-ons-fab>-->
    <v-ons-action-sheet
      :visible.sync="requested"
      cancelable
    >
      <v-ons-card style="padding: 0; margin: 0;" >
        <div class="content">
          <p style="margin-top: 10px;margin-bottom: 10px;font-size: 12px; text-align: center; color: #939393;">{{ selectedDriver.name }} is {{ selectedDriver.time }} min away</p>

          <v-ons-row style="padding: 10px;">
            <v-ons-col style="position: relative">
              <div style="position: absolute; z-index: 1; top: 0; left: 0;">
                <div @click="carDialog = true" class="vehicle"
                     :style="{ 'background-color': selectedCar.color, 'background-image': 'url(static/assets/cars/' + selectedCar.image + ')' }"></div>
                <!--<span class="car-type">{{ selectedCar.name }}</span>-->
              </div>
              <div style="width: 50px; position: relative;  z-index: 2; margin-left: 30px;">
                <img :src="selectedDriver.ppic" class="driver-image" style="height: 50px;width: 50px;border-radius: 50%;" alt="" @click="driverDialog = true">
                <span class="notification rating">4.3</span>
              </div>
            </v-ons-col>
            <v-ons-col>
              <h5 style="margin: 0;font-size: 15px;" @click="driverDialog = true">{{ selectedDriver.name }}</h5>
              <p style="margin-top: 3px;margin-bottom: 0px;font-size: 12px;">2556 rides</p>
            </v-ons-col>
            <v-ons-col style="line-height: 5px; text-align: right">
              <p style="font-size: 12px;">NISSAN GT</p>
              <h4 style="font-weight: normal">KBQ 116Y</h4>
            </v-ons-col>
          </v-ons-row>

        </div>

        <v-ons-row >
          <v-ons-col width="50%">
            <v-ons-alert-dialog-button >Contact</v-ons-alert-dialog-button>
          </v-ons-col>
          <v-ons-col width="50%">
            <v-ons-alert-dialog-button style="color: red;">Cancel</v-ons-alert-dialog-button>
          </v-ons-col>

        </v-ons-row>
      </v-ons-card>
    </v-ons-action-sheet>


    <v-ons-card v-if="meta && requested == false">
      <v-ons-row>
        <v-ons-col>
          <div class="grid-content">
            <h4 class="meta-value price">{{ estimate }}</h4>
            <span class="meta">Estimate</span>
          </div>
        </v-ons-col>
        <v-ons-col>
          <div class="grid-content statistics" style="">
            <h4 class="meta-value">{{ meta.duration.text }}</h4>
            <h4 class="meta-value">{{ meta.distance.text }}</h4>
            <span class="meta">Meta</span>
          </div>
        </v-ons-col>
        <v-ons-col>
          <div class="grid-content" id="vt-select">
            <div @click="carDialog = true" class="vehicle"
                 :style="{ 'background-color': selectedCar.color, 'background-image': 'url(static/assets/cars/' + selectedCar.image + ')' }"></div>
            <span class="car-type">{{ selectedCar.name }}</span>
          </div>
        </v-ons-col>
      </v-ons-row>
      <!--<v-ons-row style="margin-top: 15px; margin-bottom: 15px;">
        <v-ons-col width="25%">
          <div class="grid-content" style="padding: 0 0; padding-bottom: 0;padding-top: 0;">
            <div @click="carDialog = true" class="vehicle"
                 :style="{ 'background-color': selectedCar.color, 'background-image': 'url(static/assets/cars/' + selectedCar.image + ')' }"></div>
            <span class="car-type">{{ selectedCar.name }}</span>
          </div>
        </v-ons-col>
        <v-ons-col>
          <div class="grid-content">
            <v-ons-card class="box-card driver" @click="driverDialog = true">
              <table style="width: 100%;">
                <tr>
                  <td colspan="1" style="width: 50px;line-height: 0;">
                    <img :src="selectedDriver.ppic" style="height: 50px;width: 50px;border-radius: 50%;" alt="">
                  </td>
                  <td style="padding-left: 4px;">
                    <h5 style="margin: 0;font-size: 15px;">{{ selectedDriver.name }}</h5>
                    &lt;!&ndash;<p style="margin-top: 3px;margin-bottom: 0px;font-size: 12px;">{{ selectedDriver.email }}</p>&ndash;&gt;
                    <p style="margin-top: 3px;margin-bottom: 0px;font-size: 12px;">{{ selectedDriver.time }} min away</p>



                  </td>
                </tr>
              </table>
            </v-ons-card>
          </div>
        </v-ons-col>
      </v-ons-row>-->
      <v-ons-row>
        <v-ons-button @click="requestRide"  type="success" style="width: 100%">REQUEST A CAB</v-ons-button>
      </v-ons-row>
    </v-ons-card>

    <v-ons-dialog
      class="select_dialog"
      cancelable
      :visible.sync="driverDialog">
      <v-ons-list>
        <v-ons-list-header>Select Preferred Driver</v-ons-list-header>
        <v-ons-list-item v-for="driver in drivers" :key="driver.email" @click="selectDriver(driver)">
          <div class="left">
            <img :src="driver.ppic" class="list-item__thumbnail driver-image"
                 style="height: 40px;width: 40px;border-radius: 50%;" alt="">
          </div>
          <div class="center">
            <span class="list-item__title">{{ driver.name }}</span>
            <span class="list-item__subtitle">
      {{ driver.time }}min - {{ driver.distance }}Km Away</span>
          </div>
        </v-ons-list-item>
      </v-ons-list>
    </v-ons-dialog>
    <v-ons-dialog
      class="select_dialog"
      cancelable
      :visible.sync="carDialog">

      <v-ons-list>
        <v-ons-list-header>Select Vehicle Type</v-ons-list-header>
        <v-ons-list-item v-for="car in cars" :key="car.name" @click="selectCar(car)">
          <div class="left">
            <div class="list-item__thumbnail vehicle"
                 :style="{ 'background-color': car.color, 'height': '40px', 'width': '40px', 'background-image': 'url(static/assets/cars/' + car.image + ')' }"></div>
          </div>
          <div class="center">
            <span class="list-item__title">{{ car.name }}</span>
            <span class="list-item__subtitle">Car description comes here</span>
          </div>
        </v-ons-list-item>
      </v-ons-list>
    </v-ons-dialog>


    <!--LOADING-->
    <v-ons-modal :visible="modalVisible">
      <p style="text-align: center">
        <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
        <br>
        Loading
        <br><br>
        Looking for the nearest drivers
      </p>
    </v-ons-modal>

  </div>

</template>

<script>
  import {EventBus} from "../utils/bus";
  import axios from 'axios';

  export default {
    name: "Stats",
    data() {
      return {
        request: true,
        meta: null,
        requested: false, // the card for driver is coming
        driverDialog: false,
        carDialog: false,
        modalVisible: false,
        timeoutID: 0,
        cars: [
          {
            name: "Basic",
            color: "yellow",
            image: "basic.png",
            fare: {rate: 29, min: 200, permin: 4}
          },
          {
            name: "Comfort",
            color: "red",
            image: "comfort.png",
            fare: {rate: 40, min: 200, permin: 4}
          },
          {
            name: "Comfort+",
            color: "green",
            image: "comfortplus.png",
            fare: {rate: 50, min: 270, permin: 4}
          },
          {
            name: "LadyBUG",
            color: "pink",
            image: "ladybug.png",
            fare: {rate: 40, min: 200, permin: 4}
          },
          {
            name: "Goods",
            color: "blue",
            image: "goods.png",
            fare: {rate: 60, min: 300, permin: 4}
          }
        ],
        selectedCar: null,
        drivers: [
          {
            name: "Peter Kamau",
            email: "agape@live.fr",
            rating: 3.78,
            phone: "0721928129",
            distance: "3 km",
            ppic: "https://placeimg.com/70/70/people/7"
          },
          {
            name: "John Doe",
            email: "john@little.br",
            rating: 4.52,
            phone: "0711928129",
            distance: "1.24 km",
            ppic: "https://placeimg.com/70/70/people/5"
          },
          {
            name: "Paul Smith",
            email: "smith@uber.com",
            rating: 1.12,
            phone: "0700123456",
            distance: "0.85 km",
            ppic: "https://placeimg.com/70/70/people/3"
          }
        ],
        selectedDriver: null
      }
    },
    methods: {
      selectDriver(driver) {
        // alert(email);
        this.selectedDriver = driver;
        this.driverDialog = false;
      },
      selectCar(vehicle) {
        // alert(vehicle);
        this.selectedCar = vehicle;
        this.carDialog = false;
      },
      getPrefDrivers: function (source) {
        var _self = this;
        // console.log(this.selectedCar.name);
        /*var data = {
          phone: '254700928129', // Todo saved from the store
          // source: this.meta ? this.meta.origin : document.getElementById('origin-input').value,
          source: this.meta.origin,
          vtype: this.selectedCar.name
        };*/
        axios.get('https://test.little.bz/booking/load/preffred', {
          params: {
            phone: '254700928129', // Todo saved from the store
            source: this.meta ? this.meta.origin : document.getElementById('origin-input').value,
            // source: this.meta.origin,
            vtype: this.selectedCar.name
          }
        }).then(function (response) {
          _self.drivers = response.data;
          _self.selectedDriver = response.data[0];
          console.log(response.data);
        })
      },
      requestRide() {
        this.modalVisible = true;
        clearTimeout(this.timeoutID);
        this.timeoutID = setTimeout(() => {
          this.modalVisible = false;
          this.requested = true;
        }, 2000);
      }
    },
    computed: {
      estimate: function () {

        var fare = Math.ceil(this.selectedCar.fare.rate * (this.meta.distance.value / 1000) + (4 * (this.meta.duration.value / 60)) + 100);
        return (fare < this.selectedCar.fare.min) ? this.selectedCar.fare.min : fare;
      },
    },
    created: function () {
      this.selectedDriver = this.drivers[0];
      this.selectedCar = this.cars[0];
    },
    beforeMount: function () {

      var _self = this;

      EventBus.$on('meta_changes', meta => {
        this.meta = meta;
        this.fabVisible = false;
        this.getPrefDrivers();
        // console.log(this.meta);
      });

      EventBus.$on('originChange', function () {
        // console.log('hello');
        _self.getPrefDrivers();
      });

    },
    mounted: function () {

    }
  }
</script>

<style scoped>
  .button {
    text-align: center
  }

  .button.button--quiet{
    /*padding: 4px !important;*/
  }

  .list-item__title:first-letter {text-transform:uppercase}

  .list-item__title{
    text-transform: lowercase;
  }
  .list-item__title{
    /*text-transform: capitalize;*/
  }

  .rating{
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: 10px;
    font-weight: bold;
  }

  .vehicle {
    height: 50px;
    width: 50px;
    background: hsl(0, 0%, 74%);
    border-radius: 50%;
    margin-bottom: 6px;
    background-size: cover;
  }

  #vt-select {
    position: relative;
  }

  #vt-select .vehicle{
    height: 30px;
    width: 30px;
  }

  .car-type {
    /*text-align: center;*/
    /*display: block;*/
    font-size: 9px;
    font-weight: bold;
    text-transform: uppercase;

    /*display: inline-block;*/
    position: absolute;
    bottom: 50%;
    top: 9px;
    margin: auto 0;
    text-align: center;
    right: 10px;
  }

  span.meta {
    margin: 0;
    font-size: xx-small;
    text-transform: uppercase;
    font-weight: bold;
    color: #a7a7a7;
    display: block;
    text-align: center;
  }

  h4.meta-value {
    text-align: center;
    color: #0088cc;
    margin: 0.1em auto;
  }

  .statistics .meta-value{
    text-transform: uppercase;font-size: 12px !important;
  }

  h4.meta-value.price {
    text-align: left;
    font-size: x-large;
  }

  h4.meta-value.price::before {
    content: "KSH";
    font-size: xx-small;
    line-height: 0;
    color: #01131d85;
    vertical-align: middle;
    margin-right: 3px;
  }

  .driver {
    border: 1px solid #dadada;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
    padding: 2px;
  }
  .menu-icon{
    padding: 2px;
    font-size: 12px;
  }
  .menu-icon .fab--mini__icon {
    line-height: 24px !important;
  }

  .driver-image{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background: #e4e4e4;
    display: block;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQDx0u2d4l7Y3ehCB_7V_lwRnmAf_02H_D-LuvRsXowRQQLYf7A");
    background-size: contain;
  }

  .alert-dialog-button{
    width: 100%;
    font-size: 12px !important;
    text-transform: uppercase !important;
    /*font-weight: 900;*/
  }
</style>



