import { EventBus } from './bus'
      function AutocompleteDirectionsHandler(map) {
        this.map = map;
        this.originPlaceId = null;
        this.destinationPlaceId = null;
        this.travelMode = 'DRIVING';
        var originInput = document.getElementById('origin-input');
        var destinationInput = document.getElementById('destination-input');
        this.directionsService = new google.maps.DirectionsService;

        var geoDecoder = new google.maps.Geocoder;
        var that = this;
        var GLOBAL_POSITION;


        this.loadPlace = function () {
          window.setTimeout(function() {
            var geoDecoder = new google.maps.Geocoder;
            GLOBAL_POSITION = that.map.getCenter();
            geoDecoder.geocode({'location': that.map.getCenter()}, (results, status) => {

              if (status === 'OK') {
                if (results[0]) {
                  // map.setZoom(15);
                  that.directionsService.originPlaceId = results[0].place_id;

                  // _self.originAddress = results[0].formatted_address;
                  originInput.value = results[0].formatted_address;
                  // alert(results[0].formatted_address);
                  that.directionsService.route();
                } else {
                  EventBus.$ons.notification.toast('Your Locaction could not be determined')
                }
              }
            });

          }, 300);
        };

        this.map.addListener('zoom_changed', function () {
          this.panTo({ lat: GLOBAL_POSITION.lat(), lng: GLOBAL_POSITION.lng()});
          console.log(GLOBAL_POSITION.lat(), GLOBAL_POSITION.lng() );
           //that.map.setCenter(GLOBAL_POSITION);
        });


        this.map.addListener('dragend', that.loadPlace);
        this.map.addListener('dragstart', function () {
          //alert("hello")
        //  TODO: display some sort of message or toast
        });
        // this.map.addListener('bounds_changed', that.loadPlace);
        //this.map.addListener('zoom_changed', that.loadPlace);


        this.meta = {};

        const dirOptions = {
          polylineOptions: {
              strokeColor: '#0088cc',
              strokeWeight: 5
          },
          markerOptions: {
              draggable: false
          },
          suppressMarkers: true
      };

      this.directionsDisplay = new google.maps.DirectionsRenderer(dirOptions);

        this.directionsDisplay.setMap(map);

        var originAutocomplete = new google.maps.places.Autocomplete(
            originInput, {placeIdOnly: false});
        var destinationAutocomplete = new google.maps.places.Autocomplete(
            destinationInput, {placeIdOnly: false});



        this.setupPlaceChangedListener(originAutocomplete, 'ORIG');
        this.setupPlaceChangedListener(destinationAutocomplete, 'DEST');


        this.origin_marker = new google.maps.Marker({
          // position: me.meta.start_location,
          map: this.map,
          // title: 'Hello World!',
          icon: {
            url: '/static/assets/origin.svg',
            scaledSize: new google.maps.Size(10, 21),
            // size: new google.maps.Size(48, 48),
            origin: new google.maps.Point(0, 0),
            anchor: new google.maps.Point(5, 21)
          }
      });


        this.destination_marker = new google.maps.Marker({
            map: this.map,
            icon: {
              url: '/static/assets/destination.svg',
              scaledSize: new google.maps.Size(10, 21),
              origin: new google.maps.Point(0, 0),
              anchor: new google.maps.Point(5, 21)
            }
        });

      }



      AutocompleteDirectionsHandler.prototype.setupPlaceChangedListener = function(autocomplete, mode) {
        var me = this;
        autocomplete.bindTo('bounds', this.map);
        autocomplete.addListener('place_changed', function() {
          var place = autocomplete.getPlace();
          if (!place.place_id) {
            EventBus.$ons.notification.alert('Please select an option from the dropdown list.');
            return;
          }
          if (mode === 'ORIG') {
            me.originPlaceId = place.place_id;
          } else {
            me.destinationPlaceId = place.place_id;
          }
          me.route();
        });

      };

      AutocompleteDirectionsHandler.prototype.route = function() {
        if (!this.originPlaceId || !this.destinationPlaceId) {
          return;
        }
        var me = this;

        this.directionsService.route({
          origin: {'placeId': this.originPlaceId},
          destination: {'placeId': this.destinationPlaceId},
          travelMode: me.travelMode,
          provideRouteAlternatives: true,
        }, function(response, status) {
          if (status === 'OK') {

            // console.log(response);

            me.meta.distance = response.routes["0"].legs["0"].distance;
            me.meta.duration = response.routes["0"].legs["0"].duration;
            me.meta.origin = response.routes["0"].legs["0"].start_address;
            me.meta.destination = response.routes["0"].legs["0"].end_address;

            // console.log(response);

            // console.log(me.meta);

            EventBus.$emit('meta_changes', me.meta);


            me.directionsDisplay.setDirections(response);

            me.origin_marker.setTitle(response.routes["0"].legs["0"].start_address);
            me.destination_marker.setTitle(response.routes["0"].legs["0"].end_address);


            me.origin_marker.setPosition(response.routes["0"].legs["0"].start_location);
            me.destination_marker.setPosition(response.routes["0"].legs["0"].end_location);


          } else {
            EventBus.$ons.notification.alert('Directions request failed due to ' + status);

          }
        });
      };

export default AutocompleteDirectionsHandler;



