<template>
  <v-ons-page>
    <v-ons-toolbar class="home-toolbar" modifier="transparent cover-content">
      <div class="left">
        <v-ons-toolbar-button @click="menuToggle()">
          <!--<v-ons-icon icon="fa-bars"></v-ons-icon>-->
          <v-ons-icon icon="ion-navicon, material:md-more-vert"></v-ons-icon>

        </v-ons-toolbar-button>
      </div>
      <div class="center">{{ msg }}</div>
      <div class="right">
        <v-ons-toolbar-button>
          <v-ons-icon icon="ion-ios-information-outline"></v-ons-icon>
        </v-ons-toolbar-button>
      </div>
    </v-ons-toolbar>


    <div id="mapcanvas">
    </div>

  <v-ons-card id="locator">
      <locator></locator>
    </v-ons-card>


    <stats id="request"></stats>
    <img class="map-pin" src="../assets/pin.svg" alt="Little">


  </v-ons-page>
</template>

<script>
  import Stats from '../components/Stats'
  import Locator from '../components/Locator'
  import STORE from '../utils/store'

  export default {
  name: 'home',
  components: {
    Stats,
    Locator
  },
  data () {
    return {
      msg: 'Homepage',
    }
  },
  methods: {
    menuToggle(){
      STORE.commit('splitter/toggle')
    }
  },
    mounted: function () {
      this.$ons.notification.toast('Awesome! all loaded', {timeout: 2000})
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#mapcanvas{
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  z-index: 2;
}
#locator{
  position: absolute;
  z-index: 3;
  left: 0;
  right: 0;
  padding: 0;
  margin: 20px;
  box-shadow: 0 12px 24px 4px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  /*box-shadow: 0 0.46875rem 2.1875rem rgba(90,97,105,.1), 0 0.9375rem 1.40625rem rgba(90,97,105,.1), 0 0.25rem 0.53125rem rgba(90,97,105,.12), 0 0.125rem 0.1875rem rgba(90,97,105,.1);*/
}

#request{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
}
  .card{
    /*background-color: rgba(255, 255, 255, 0.4) !important;*/
  }

  .location-input{
    width: 100%;
    padding: 10px 14px;
    background: transparent;
    border: none;
    font-size: medium;
  }

  .map-pin{
    z-index: 999;
    position: absolute;
    height: 50px;
    top: 50%;
    left: 50%;
    margin-left: -25px;
    margin-top: -50px;
  }

</style>



