<template>
  <div>
    <v-ons-row style="position: relative;">
      <v-ons-icon class="input-icon" icon="ion-ios-circle-filled"></v-ons-icon>
      <input id="origin-input" placeholder="Select Pickup"
      />
    </v-ons-row>
    <v-ons-row style="position: relative;">
      <v-ons-icon class="input-icon" icon="ion-ios-circle-outline"></v-ons-icon>
      <input id="destination-input" @focus="notifyOriginChange" placeholder="Select Destination"/>
    </v-ons-row>


  </div>
</template>

<script>
  import {custom} from '../utils/mapStyles'
  import {EventBus} from '../utils/bus'
  import STORE from '../utils/store'
  import AutocompleteDirectionsHandler from '../utils/DirectionService'
  const { Swipe, SwipeItem } = require('vue-swipe');

  export default {
    name: "Locator",
    components: {
      'swipe': Swipe,
      'swipe-item': SwipeItem
    },
    mounted: function () {

      let mapOptions = {
        zoom: 15,
        disableDefaultUI: true,
        center: new google.maps.LatLng(-1.2680358, 36.8011286),
        // mapTypeId: google.maps.MapTypeId.TERRAIN
      };
      var map = new google.maps.Map(document.getElementById('mapcanvas'), mapOptions);

      map.setOptions({styles: custom});

      STORE.dispatch('map/setMap', map).then(function () {

        /*let trafficLayer = new google.maps.TrafficLayer();
        trafficLayer.setMap(map);*/

        var directionService = new AutocompleteDirectionsHandler(map);

        STORE.dispatch('map/setDirectionSerive', directionService).then(function () {
          // If location has been allowed
          if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition((position) => {
              mapOptions.center = new google.maps.LatLng(position.coords.latitude, position.coords.longitude)

              mapOptions.zoom = 13;

              map.setOptions(mapOptions);

              const geocoder = new google.maps.Geocoder;

              var myLocation = new google.maps.Marker({
                position: mapOptions.center,
                map: map,
                icon: {
                  url: '/static/assets/location.png',
                  scaledSize: new google.maps.Size(48, 48),
                  // size: new google.maps.Size(48, 48),
                  origin: new google.maps.Point(0, 0),
                  anchor: new google.maps.Point(24, 24)
                },
                title: "My Current Location"
              });

              STORE.dispatch('map/setMyLocation', myLocation);

              var _self = this;

              geocoder.geocode({'location': mapOptions.center}, (results, status) => {
                var bounds  = new google.maps.LatLngBounds();
                bounds.extend(mapOptions.center);

                if (status === 'OK') {
                  if (results[0]) {

                    map.setZoom(15);
                    // map.fitBounds(bounds);
                    map.panToBounds(bounds);

                    directionService.originPlaceId = results[0].place_id;
                    // _self.originAddress = results[0].formatted_address;
                    document.getElementById('origin-input').value = results[0].formatted_address;
                    directionService.route();
                  } else {
                    EventBus.$ons.notification.toast('Your Locaction could not be determined')
                  }
                }
              });

            });

          }



          // END Location allowed
        });
      });


    },
    methods: {
      notifyOriginChange:  function () {
        EventBus.$emit('originChange');
      }
    }
  }
</script>

<style scoped>
  #origin-input, #destination-input {
    width: 100%;
    padding: 10px 14px;
    background: transparent;
    border: none;
    font-size: 14px;
    color: rgba(31, 31, 33, 0.83);
    padding-left: 35px;
  }

  .input-icon{
    position: absolute;
    top: 10px;
    left: 14px;
    color: #ccc;
  }


  .my-swipe {
    height: 200px;
    color: #fff;
    font-size: 30px;
    text-align: center;
  }



</style>
<style>
  .pac-icon, .pac-icon-marker {
    display: none !important;
  }

  /*.pac-item {
    padding: 10px 14px !important;
  }
*/
  .pac-item {
    padding: 2px 14px !important;
  }
  .pac-item-query {
    font-size: 15px;
  }

</style>



